import EmptyWishList from "../emptyWishlist/empty";
import WishList from "../wishList/wishlist";
import { useState, useEffect } from "react";

function MainWishList() {
  const [lovedProducts, setLovedProducts] = useState([]);

  useEffect(() => {
    const saved = localStorage.getItem("lovedproducts");
    if (saved) {
      setLovedProducts(JSON.parse(saved));
    }
  }, []);
  return (
    <div className="mainWishlist">
      <div className="mainwish-header">
        <h2>Your Loved Products</h2>
        <p>Products you've added to your wishlist</p>
      </div>

      {lovedProducts.length > 0 ? (<WishList product={lovedProducts} />) :
      (<EmptyWishList/>)
      }
    </div>
  );
}
export default MainWishList;
